import Vue from "vue";
import Router from "vue-router";
import ProductIndex from "./components/ProductIndex.vue";

Vue.use(Router);

let routes = [
  {
    path: '/',
    name: 'homeLink',
    component: ProductIndex,
    redirect: '/product-tabs',
    meta: {
      title: "零部件管理"
    },
    children: [
      { path: '/product-classify', name: 'productclassifyLink', component: () => import('./components/ClassifyProduct'), meta: { title: "零部件分类管理" } },
      { path: '/product-enterprise', name: 'productEnterprise ', component: () => import('./components/EnterpriseInfo.vue'), meta: { title: "企业信息" } },
      // { path: '/product-tabs', name: 'product', component: () => import('./components/ProductTabs'), meta: { title: "零部件详情Tabs" } },
      { path: '/product-tabs', name: 'product', component: () => import('@/views/products/tabs'), meta: { title: '零部件管理' } },
      { path: '/product-list', name: 'productlistLink', component: () => import('./components/ProductList'), meta: { title: "零部件管理" } },
      { path: '/product-add', name: 'productaddLink', component: () => import('./components/AddProducts'), meta: { title: "添加零部件" } },
      { path: '/product-info/:productId', name: 'productinfoLink', component: () => import('./components/ProductInfo'), meta: { title: "零部件详情" } },
      { path: '/process', name: 'process', component: () => import('./views/craft/info.vue'), meta: { title: '工序信息' } },
      { path: '/technology', name: 'technology', component: () => import('./views/craft/tabs'), meta: { title: '工艺信息' } },
      { path: '/check', name: 'check', component: () => import('./views/check/tabs'), meta: { title: '零部件审核' } },
      {
        path: '/test', name: 'test', component: () => import('@/views/test')
      }
    ]
  }
]

export default new Router({
  //mode: "history",
  base: process.env.BASE_URL,
  routes,
  mode: 'hash'
});
