import Vue from "vue";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import Antd from "ant-design-vue";
import "ant-design-vue/dist/antd.css";
import VueWechatTitle from "vue-wechat-title";
import Axios from "axios";
import "./assets/master.css";
import  GlobalData from './utils/Constant'
import components from '@/components'
Vue.use(components);
Vue.use(Antd);

Vue.prototype.$axios = Axios;

Vue.use(VueWechatTitle);

Vue.config.productionTip = false;

Vue.prototype.$global = GlobalData;

router.beforeEach((to, from, next) => {
  GlobalData.access_token=to.query.access_token
  // GlobalData.access_token=''
  // GlobalData.access_token='vBdPjuU9vH38946OpX3f4szwrqzVv0jtXKaPaY7BZuGHryICKx0NW69NhRKkF2W2ZWWPB1B12l4aVRr0laBmKzJtOOPJgslp0129HoqmkQgjnHvonMt67Zbdfswlt58q'
  next();
  return;
  if (!to.meta.auth) {
    if (Cookies.get(GlobalData.requestToken)) {
      next();
    } else {
      next({"path": "/login"});
    }
  } else {
    next();
  }
});

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount("#app");
